
"use client";

import * as React from "react";
import Image from "next/image";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { MapPin, Loader2 } from "lucide-react"; // Added Loader2

interface MapPlaceholderProps {
  latitude?: number | null;
  longitude?: number | null;
  locationName?: string | null;
  isFetchingLocation?: boolean; // Added prop
}

// Basic debounce function
function debounce<F extends (...args: any[]) => void>(func: F, wait: number): (...args: Parameters<F>) => void {
  let timeoutId: ReturnType<typeof setTimeout> | null = null;
  return function(this: ThisParameterType<F>, ...args: Parameters<F>) {
    if (timeoutId !== null) {
      clearTimeout(timeoutId);
    }
    timeoutId = setTimeout(() => func.apply(this, args), wait);
  };
}


export function MapPlaceholder({ latitude, longitude, locationName, isFetchingLocation }: MapPlaceholderProps) {
  const [mapUrl, setMapUrl] = React.useState<string | null>(null);
  const [isGeneratingMap, setIsGeneratingMap] = React.useState<boolean>(false); // Renamed state
  const [error, setError] = React.useState<string | null>(null);

  const generateMapUrl = React.useCallback(async (lat: number, lon: number) => {
     setIsGeneratingMap(true); // Use renamed state
     setError(null);
     // Placeholder: In a real scenario, you might fetch a static map image URL
     // For now, we use picsum.photos as a stand-in, using coords slightly to vary the image.
     // A better approach would be a static map API (like Mapbox or Google Static Maps)
     // but that requires API keys and setup.
     try {
        // Simulate fetching a map image
        await new Promise(resolve => setTimeout(resolve, 500)); // Simulate network delay
        const width = 600;
        const height = 400;
        // Use coordinates to slightly change the image seed for variation
        const seed = Math.round(lat * 100 + lon * 100);
         setMapUrl(`https://picsum.photos/seed/${seed}/${width}/${height}`);
     } catch (e) {
         setError("Could not load map image.");
         console.error("Map generation error:", e);
     } finally {
        setIsGeneratingMap(false); // Use renamed state
     }
  }, []);

  // Debounced version of generateMapUrl
   const debouncedGenerateMapUrl = React.useMemo(
       () => debounce(generateMapUrl, 1000),
       [generateMapUrl]
     );


  React.useEffect(() => {
      // Only generate map if not fetching location via button
      if (!isFetchingLocation && latitude != null && longitude != null) {
         debouncedGenerateMapUrl(latitude, longitude);
      } else {
         setMapUrl(null); // Clear map if no coordinates or fetching location
         setIsGeneratingMap(false); // Ensure generation state is reset
         setError(null);
      }
      // Cleanup debounce timer on unmount or dependency change
      return () => {
           if ((debouncedGenerateMapUrl as any).cancel) {
              (debouncedGenerateMapUrl as any).cancel(); // If debounce library supports cancel
           }
         };
    }, [latitude, longitude, isFetchingLocation, debouncedGenerateMapUrl]); // Added isFetchingLocation dependency


  return (
    <Card className="shadow-sm">
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <MapPin className="h-5 w-5 text-primary" />
          Location Visualizer
        </CardTitle>
      </CardHeader>
      <CardContent>
         {/* Show loader specifically when fetching via button */}
         {isFetchingLocation && (
           <div className="aspect-video w-full flex items-center justify-center bg-muted/50 rounded-md border border-dashed">
             <div className="text-center">
               <Loader2 className="h-6 w-6 animate-spin mx-auto text-primary mb-2"/>
               <p className="text-muted-foreground text-sm">Getting current location...</p>
             </div>
           </div>
         )}

         {/* Show map generation loader only if not fetching location */}
         {!isFetchingLocation && isGeneratingMap && (
             <div className="aspect-video w-full flex items-center justify-center bg-muted/50 rounded-md border border-dashed">
               <p className="text-muted-foreground text-sm">Loading map preview...</p>
              </div>
          )}

        {error && <p className="text-destructive text-sm">{error}</p>}

        {/* Show map only if NOT fetching location, NOT generating map, no error, and mapUrl exists */}
        {!isFetchingLocation && !isGeneratingMap && !error && mapUrl && latitude != null && longitude != null && (
          <div className="aspect-video w-full relative overflow-hidden rounded-md border">
             <Image
                 src={mapUrl}
                 alt={`Map placeholder for ${locationName || 'selected location'}`}
                 fill
                 style={{ objectFit: "cover" }}
                 data-ai-hint="map aerial view"
             />
          </div>
        )}

         {/* Show placeholder text if not loading and no map is ready */}
         {!isFetchingLocation && !isGeneratingMap && !error && !mapUrl && (
          <div className="aspect-video w-full flex items-center justify-center bg-muted/50 rounded-md border border-dashed">
            <p className="text-muted-foreground text-center p-4 text-sm">
              {locationName && !/Coords:/.test(locationName)
                ? `Map preview will appear here for ${locationName}.`
                : locationName && /Coords:/.test(locationName)
                ? 'Map preview appears when using "Use Current Location".'
                : "Enter a location or use current location to see map preview."}
            </p>
          </div>
        )}

         {/* Show location name/coords only if not fetching location */}
         {!isFetchingLocation && locationName && <p className="text-sm text-muted-foreground mt-2">Showing map for: {locationName}</p>}
         {!isFetchingLocation && latitude != null && longitude != null && (
           <p className="text-xs text-muted-foreground">Lat: {latitude.toFixed(4)}, Lon: {longitude.toFixed(4)}</p>
         )}
      </CardContent>
    </Card>
  );
}
